  display: block;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

:root {
  --main-color: #fff;
  --second-color: #000;
  --information-color: #ff001b;
  --accent-color: #ff6b08;
  --footer-bg-color: #f7f7f7;
  --footer-text-color: #545454;
}

.container {
  width: 100%;
  outline: 1px solid green;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.section {
  outline: 1px solid red;
}

.header__container {
  width: 100%;
  background-color: #000;
  background-image: linear-gradient(rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("header-mobile-x1.dd321514.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-grow: 1;
  padding-top: 42px;
  padding-bottom: 64px;
}

@media screen and (min-width: 768px) {
  .header__container {
    background-image: linear-gradient(rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("header-tablet-x1.f909f4aa.png");
    padding-top: 40px;
    padding-bottom: 60px;
  }
}

@media screen and (min-width: 1280px) {
  .header__container {
    background-image: linear-gradient(rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("header-desktop-x1.9253c571.png");
  }
}

.header__container.header-home__container {
  text-align: center;
  background-image: linear-gradient(rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("index-mobile-x1.ffa78c7a.png");
  padding-bottom: 92px;
}

@media screen and (min-width: 768px) {
  .header__container.header-home__container {
    background-image: linear-gradient(rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("index-tablet-x1.be18f143.png");
    padding-top: 40px;
    padding-bottom: 80px;
  }
}

@media screen and (min-width: 1280px) {
  .header__container.header-home__container {
    background-image: linear-gradient(rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("index-desktop-x1.10ed511b.png");
  }
}

.header__navigation {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 62px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__navigation {
    align-items: baseline;
    margin-bottom: 40px;
  }
}

.header__img {
  width: 100%;
}

.logo {
  padding: 10px;
}

@media screen and (min-width: 768px) {
  .logo {
    flex-direction: row;
    align-items: center;
    display: flex;
  }
}

.logo__svg {
  color: var(--main-color);
}

@media screen and (min-width: 768px) {
  .logo__svg {
    margin-right: 12px;
  }
}

.logo__text {
  display: none;
}

@media screen and (min-width: 768px) {
  .logo__text {
    color: var(--main-color);
    font-family: Roboto;
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: 35px;
    display: block;
  }
}

.header__list {
  display: flex;
}

.header__li {
  margin-right: 40px;
  padding: 5px;
}

.header__li:last-child {
  margin-right: 0;
}

.header__link {
  text-transform: uppercase;
  color: var(--main-color);
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

.header__link:visited {
  color: var(--main-color);
}

.current:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #ff001b;
  display: block;
  position: relative;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.header__btns {
  flex-direction: row;
  justify-content: center;
  display: flex;
}

.header__btn {
  border: 1px solid var(--main-color);
  height: 44px;
  width: 130px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin-right: 20px;
  padding: 13px 36px;
}

.header__btn:last-child {
  margin-right: 0;
}

.header__btn:hover, .header__btn:focus {
  background: #ff6b01;
  border: 1px solid #ff6b01;
  box-shadow: 0 0 100px -24px #ff6b01;
}

@media screen and (min-width: 768px) {
  .header__btn {
    width: 152px;
    margin-right: 32px;
    padding: 13px 46px;
  }
}

@media screen and (min-width: 1280px) {
  .header__btn {
    width: 148px;
    margin-right: 16px;
    padding: 13px 41.5px;
  }
}

.header__search {
  width: 280px;
  display: inline-flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header__search {
    width: 336px;
  }
}

@media screen and (min-width: 1280px) {
  .header__search {
    width: 394px;
  }
}

.search__input {
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  border-top: none;
  border-left: none;
  border-right: none;
}

.icon-search {
  z-index: 5;
  position: absolute;
  right: 0;
}

