.header__container {
    width: 100%;
    padding-top: 42px;
    padding-bottom: 64px;
    background-color: black;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    flex-grow: 1;
    background-color: #000;
    background-image: linear-gradient(rgba(47, 48, 58, 0.4),
            rgba(47, 48, 58, 0.4)),
        url('../images/mobile/header-mobile-x1.png');

    @media screen and (min-width: $tablet) {
        padding-top: 40px;
        padding-bottom: 60px;
        background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                rgba(47, 48, 58, 0.4)),
            url('../images/tablet/header-tablet-x1.png');
    }

    @media screen and (min-width: $desktop) {
        background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                rgba(47, 48, 58, 0.4)),
            url('../images/desktop/header-desktop-x1.png');
    }
}
.header__container.header-home__container{
    text-align: center;
    padding-bottom: 92px;
        background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                    rgba(47, 48, 58, 0.4)),
                url('../images/mobile/index-mobile-x1.png');
        
            @media screen and (min-width: $tablet) {
                padding-top: 40px;
                padding-bottom: 80px;
                background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                        rgba(47, 48, 58, 0.4)),
                    url('../images/tablet/index-tablet-x1.png');
            }
        
            @media screen and (min-width: $desktop) {
                background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                        rgba(47, 48, 58, 0.4)),
                    url('../images/desktop/index-desktop-x1.png');
            }
}

.header__navigation{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 62px;

    @media screen and (min-width: $tablet){
        margin-bottom: 40px;
        align-items: baseline;;
    }
}

.header__img{
    width: 100%;
}

.logo{
    padding: 10px;
    @media screen and (min-width: $tablet){
        display: flex;
        flex-direction: row;
        align-items: center;
    }
}

.logo__svg{
    color: var(--main-color);
    @media screen and (min-width: $tablet){
        margin-right: 12px;
    }
    
}

.logo__text{
    display: none;

    @media screen and (min-width: $tablet){
        display: block;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 500;
        font-size: 30px;
        line-height: 35px;
        color: var(--main-color);
    }
}

.header__list{
    display: flex;
}

.header__li{
    margin-right: 40px;
    padding: 5px;
    
    &:last-child{
        margin-right: 0;
    }
}

.header__link{
    &:visited {
            color: var(--main-color);
        }
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase;
    color: var(--main-color);
    
}

.current::after{
    position: relative;
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    content: "";
    width: 100%;
    height: 3px;
    background-color: #FF001B;
}

.header__btns{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.header__btn{
    border: 1px solid var(--main-color);
    border-radius: 5px;
    background-color: transparent;
    padding: 13px 36px;
    margin-right: 20px;
    height: 44px;
    width: 130px;

    &:last-child{
        margin-right: 0;
    }

    &:hover,
    &:focus{
        background: #FF6B01;
        border: 1px solid #FF6B01;
        -webkit-box-shadow: 0px 0px 100px -24px rgba(255, 107, 1, 1);
        -moz-box-shadow: 0px 0px 100px -24px rgba(255, 107, 1, 1);
        box-shadow: 0px 0px 100px -24px rgba(255, 107, 1, 1);
    }

    @media screen and (min-width: $tablet){
        width: 152px;
        padding: 13px 46px;
        margin-right: 32px;
    }

    @media screen and (min-width: $desktop){
        width: 148px;
        padding: 13px 41.5px;
        margin-right: 16px;
    }
}

.header__search{
    position: relative;
    display: inline-flex;
 
    width: 280px;
        @media screen and (min-width: $tablet) {
                width:336px;
                   }
        
            @media screen and (min-width: $desktop) {
                width: 394px;
                   }
}

.search__input{
    width: 100%;
    background-color: transparent;
    border-top: none;
    border-right: none;
    border-left: none;

}
.icon-search{
    position: absolute;
    right: 0;
    z-index: 5;
    // stroke: var(--main-color);
}